import React, { useEffect, useState } from "react";
import "./Compstyle.css";
import millify from "millify";
import { Link } from "react-router-dom";
import Currency from "./Currency";
import News from "./News";

const url =
  "https://coinranking1.p.rapidapi.com/coins?referenceCurrencyUuid=yhjMzLPhuIDl&timePeriod=24h&tiers%5B0%5D=1&orderBy=marketCap&orderDirection=desc&limit=50&offset=0";

const options = {
  method: "GET",
  headers: {
    "X-RapidAPI-Key": "d06909e8bdmsh3ffd7c96efa518dp15b850jsn4c7a7ef81e8f",
    "X-RapidAPI-Host": "coinranking1.p.rapidapi.com",
  },
};

const Home = () => {
  const [coins, setCoins] = useState([]);
  const [stats, setStats] = useState([]);

  const getCoins = async () => {
    const response = await fetch(url, options);
    const tempcoins = await response.json();

    const stats = tempcoins.data.stats;
    const coins = tempcoins.data.coins;
    console.log(stats);
    setCoins(coins);
    setStats(stats);
  };

  useEffect(() => {
    getCoins();
  }, []);

  return (
    <>
      <div className="page-loader">
        <div className="back-loader">
          <div className="building-blocks">
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
          </div>
        </div>
      </div>

      <div className="home-container">
        <div className="top">
          <div>
            <p>Total CryptoCurrencies</p>
            <h2>{stats.totalCoins}</h2>
          </div>
          <div className="markets">
            <p>Total Markets</p>
            <h2>80K</h2>
          </div>
        </div>
        {/* TOP 10 */}
        <div className="crypto">
          <div className="heading">
            <p id="top10">Top 10 Cryptos In The World</p>
            <Link to="/Currency">
              <p className="showmore">Show More</p>
            </Link>
          </div>

          <div className="grid">
            {coins.map((obj) => {
              const { uuid, name, iconUrl, rank, btcPrice, marketCap, change } =
                obj;

              // console.log("OBJ", obj);
              if (rank <= 10) {
                return (
                  <div className="items" key={uuid}>
                    <div className="aboveline">
                      <p className="rank-name">{name}</p>
                      <img className="icon" src={iconUrl} alt="" />
                    </div>
                    <hr />
                    <div className="data">
                      <p>Price : {millify(btcPrice)}</p>
                      <p>Market Cap : {millify(marketCap)}</p>
                      <p>Daily Change : {change}</p>
                    </div>
                  </div>
                );
              }
            })}
          </div>
        </div>
      </div>
      <News />
    </>
  );
};

export default Home;
