import React, { useEffect, useState } from "react";
import "./Compstyle.css";
import moment from "moment";
const options = {
  method: "GET",
  headers: {
    "X-BingApis-SDK": "true",
    "X-RapidAPI-Key": "d06909e8bdmsh3ffd7c96efa518dp15b850jsn4c7a7ef81e8f",
    "X-RapidAPI-Host": "bing-news-search1.p.rapidapi.com",
  },
};

const url =
  "https://bing-news-search1.p.rapidapi.com/news/search?q=Cryptocurrency&safeSearch=Off&textFormat=Raw&freshness=Day";

const News = () => {
  const [news, setNews] = useState([]);

  const getNews = async () => {
    const response = await fetch(url, options);
    const tempNews = await response.json();
    const news = tempNews.value;
    console.log(news);
    setNews(news);
  };

  useEffect(() => {
    getNews();
  }, []);

  return (
    <>
      <div className="page-loader">
        <div className="back-loader">
          <div className="building-blocks">
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
          </div>
        </div>
      </div>
      <div className="news-container">
        <h2 id="crypto-news">Crypto News</h2>
        <div className="news-grid">
          {news.map((crypto) => {
            const { name, description, url, image, datePublished, provider } =
              crypto;

            // console.log(image.thumbnail.contentUrl);
            return (
              <div className="news-items" key={datePublished}>
                <a target="_blank" href={url}>
                  <div className="aboveline">
                    <p className="cryptoheadline">{name}</p>
                    <img src={image.thumbnail.contentUrl} />
                  </div>
                  <div className="news-desc">
                    <p>{description}</p>
                  </div>{" "}
                </a>
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
};

export default News;
