import React from "react";
import "./style.css";
import "./Components/Loader.css";
import logo from "./logo.png";
import Home from "./Components/Home";

import News from "./Components/News";
import bars from "./bars.svg";
import cross from "./xmark.svg";
import {
  BrowserRouter as Router,
  Navigate,
  Route,
  Link,
  Routes,
} from "react-router-dom";
import Currency from "./Components/Currency";

const Navbar = () => {
  return (
    <>
      <Router>
        <div className="main1">
          <div className="intro">
            <p>Welcome To Crytoverse.</p>
          </div>

          <div className="main">
            <div className="left">
              <div className="nav-bar" id="nav-bar">
                <div className="navs">
                  <div className="sidenavbar">
                    <img
                      className="bars"
                      id="bars"
                      src={bars}
                      alt=""
                      onClick={() => {
                        document.getElementById("nav-bar").style.width =
                          "250px";
                        document.getElementById("bars").style.display = "none";
                      }}
                    />

                    <img
                      className="cross"
                      src={cross}
                      alt=""
                      onClick={() => {
                        document.getElementById("nav-bar").style.width = "0px";
                        document.getElementById("bars").style.display = "block";
                      }}
                    />
                  </div>
                  <div className="nav-content">
                    <div className="logo">
                      <img src={logo} alt="" />
                      <p className="logo-title">Cryptoverse</p>
                    </div>
                    <div className="nav">
                      <ul className="list">
                        <li className="list-items">
                          <Link to="/Home">
                            <i className="fa-solid fa-house"></i>Home
                          </Link>
                        </li>
                        <li className="list-items">
                          <Link to="/Currency">
                            <i className="fa-solid fa-chart-line"></i>Crypto
                            Currencies
                          </Link>
                        </li>

                        <li className="list-items">
                          <Link to="/News">
                            <i className="fa-regular fa-lightbulb"></i>News
                          </Link>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
                <div className="creds">
                  <p id="creds">
                    Made with ❤️{" "}
                    <a target="_blank" href="https://www.shivanshsoni.com/">
                      By SHIVANSH SONI{" "}
                    </a>
                  </p>
                </div>
              </div>
            </div>
            {/* NAV BAR ENDS */}
            <div className="right">
              <Routes>
                <Route
                  path="/portfolio"
                  element={() => {
                    window.location.href = "https://www.shivanshsoni.com/";
                    return null;
                  }}
                />
                <Route path="/Home" element={<Home />} />
                <Route path="/" element={<Home />} />

                <Route path="/Currency" element={<Currency />} />
                <Route path="/News" element={<News />} />
              </Routes>
            </div>
          </div>
        </div>
      </Router>
    </>
  );
};

const Temp = () => {
  return (
    <>
      <h2>TEMP</h2>
    </>
  );
};

export default Navbar;
